h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Fredoka One" !important;
}

.App {
  text-align: center;
  color: black;
}

.welcome-hero-logo {
  margin: 0 auto !important;
  width: 150px;
  display: block;
}

.welcome-div {
  background-color: #60bbff;
  display: block;
}

h1 {
  font-size: 25px !important;
}

#return-banner-title {
  text-align: "center";
  align-content: "center";
  justify-content: "center";
  margin-top: "15px";
}

.custom-btn {
  background-color: #6d963e !important;
  border: 0px solid !important;
  color: #ffffff;
  box-shadow: 0 0 2px #000000 !important;
}

.custom-btn:hover {
  background-color: #6d963e !important;
  border: 0px solid !important;
  color: #000000 !important;
  box-shadow: 0 0 2px #000000 !important;
}

.custom-btn-video {
  color: #ffffff !important;
  background-color: #233157 !important;
  width: 100%;
  padding-top: 3%;
}

.custom-btn-video:hover {
  background-color: rgb(56, 77, 129) !important;
}

.modal-90w {
  width: 90%;
  max-width: none;
}

.modal-contents-80w {
  width: 80%;
  max-width: none;
}

.TourPageCard {
  textdecoration: "none";
  color: "red";
  borderradius: "1em";
  textalign: "left";
  margin: 2%;
}
